<template>
  <div class="label-content" v-loading="loading">
    <div class="left">
      <el-input
        placeholder="输入关键字进行过滤"
        v-model="filterText">
      </el-input>
      <el-tree
        class="filter-tree"
        :data="TreeData"
        :props="defaultProps"
        default-expand-all
        :filter-node-method="filterNode"
        @node-click="handleNodeClick"
        highlight-current
        :expand-on-click-node="false"
        ref="tree">
      </el-tree>
    </div>
    <div class="right">
        <keyword-definition title="相关动作" @update="searchWordsFn"
                            :label="label" type="verb" :keywords="behavior"></keyword-definition>
        <keyword-definition title="相关名词" @update="searchWordsFn"
                            :label="label" type="noun" :keywords="entity"></keyword-definition>
        <keyword-definition title="非相关词（若文本中出现该词组，则拒绝判断为此标签）"
                            @update="searchWordsFn"
                            :label="label" type="overcome" :keywords="phrase"></keyword-definition>
    </div>
  </div>
</template>

<script>
import keywordDefinition from '@/components/keywordDefinition.vue';
import labels from '@/api/labels';
import words from '@/api/words';

export default {
  data() {
    return {
      filterText: '',
      label: '',
      loading: false,
      defaultProps: {
        children: 'children',
        label: 'label',
      },
      typeList: [],
      TreeData: [],
      behavior: [],
      entity: [],
      phrase: [],
    };
  },
  components: {
    keywordDefinition,
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
    label() {
      this.searchWordsFn();
    },
  },
  mounted() {
    this.searchLabelFn();
  },
  methods: {
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    searchLabelFn() {
      this.loading = true;
      labels.getAllType().then(({ data: { data } }) => {
        this.loading = false;
        this.typeList = data;
        this.processFn();
      }).catch(() => {
        this.loading = false;
      });
    },
    searchWordsFn() {
      this.loading = true;
      words.get({
        label: this.label,
      }).then(({ data: { data } }) => {
        this.loading = false;
        try {
          const { verb, noun, overcome } = data;
          this.behavior = verb;
          this.entity = noun;
          this.phrase = overcome;
        } catch (e) {
          console.log(e);
        }
      }).catch(() => {
        this.loading = false;
      });
    },
    processFn() {
      const { type1 } = this.typeList;
      this.TreeData = this.setChildFn(type1, 1);
      this.$nextTick(() => {
        // 默认选中tree树的第一个标签
        const dom = document.querySelector('.el-tree-node__content');
        dom.click();
      });
    },
    setChildFn(object, deep) {
      const arr = [];
      Object.keys(object).forEach((key) => {
        const value = object[key];
        arr.push({
          id: value,
          label: key,
          children: this.findChildFn(value, deep + 1),
        });
      });
      return arr;
    },
    findChildFn(id, deep) {
      const type = this.typeList[`type${deep}`];
      if (!type) return [];
      const object = type[id];
      if (!object) return [];
      return this.setChildFn(object, deep);
    },
    handleNodeClick(irrelevant, node) {
      this.label = this.assemblyFn(node);
    },
    assemblyFn(node) {
      const { parent, label } = node;
      if (!parent) return label;
      const parentLabel = this.assemblyFn(parent);
      if (parentLabel) return `${parentLabel}_${label}`;
      return label;
    },
  },
};
</script>

<style scoped lang="less">
  .label-content {
    display: flex;
    .left {
      width: 275px;
      padding: 24px;
      background: #fff;
      box-sizing: border-box;
      .filter-tree {
        margin-top: 20px;
      }
    }
    .right {
      flex: 1;
      padding: 24px;
      margin-left: 10px;
      background: #fff;
      box-sizing: border-box;
    }
  }
</style>
